				"html": "<br />\n<br />\n<h1>Troubleshooting</h1>\n<br />\n<p>Over the past year, we’ve noticed a few errors (or noticed ourselves making user\nerrors) when using POSM. Here’s a quick list of these issues and how to solve\nthem.</p>\n<br />\n<br />\n<h3>First-time POSM install fails</h3>\n<p>Did you prepare the USB stick, plug it into POSM, and try to boot… only to get\nan error message? This can be an issue if your USB stick has an overall folder\nthat contains everything that you extracted from the POSM <code>.iso</code> file. It can\nalso be an issue if you extracted the files and then moved them manually onto\nthe USB stick. There are hidden files in the package which will not follow if\nyou try to manually move the contents. You can fix this by using an extraction\ntool (like The Unarchiver for Mac or 7-Zip for Windows) to extract the contents\ndirectly onto the USB stick.</p>\n<h3>Re-installing the core POSM software</h3>\n<p>Let’s say there’s a new version of the POSM stack and you want to upgrade. You\ncan do this by following the normal first-time install process: download\nsoftware, prep USB stick, install onto POSM. The software will overwrite and\nprevious versions.</p>\n<h3>I’m connected to POSM. When I try to reach any website, it should redirect me to posm.io but it sometimes doesn’t</h3>\n<p>Secure websites (ie websites whose URL starts with https:// instead of http://)\nwill not redirect to the posm.io homepage. So, if you click any link in your\nbookmarks, it may not redirect to posm.io.</p>\n<h3>Can I be connected to the internet and POSM at the same time?</h3>\n<p>Yes. Plug POSM into Ethernet and, in the admin interface, turn on Bridge Mode.</p>\n<h3>Can I use encrypted ODK forms to protect sensitive data?</h3>\n<p>Yes. You will need to download and set up ODK Briefcase to do this.\n<a href=\"https://opendatakit.org/help/encrypted-forms/\">Instructions are on the ODK\nsite</a>. You will need to set up a\npublic and private key and include the public key in your ODK form. To access\nsubmissions, open ODK Briefcase and <code>Pull</code> the submissions, then <code>Export</code> them.\nYou will get a <code>.csv</code> file with your decrypted data.</p>\n<h3>Multiple deployments are buggy</h3>\n<p>POSM sometimes has a hard time dealing with multiple deployments - it may\nstruggle when making MBTiles archives, or when first loading a deployment onto\nphones. We’ve solved this by just trying those processes again. It is also a\ngood idea to keep the POSM “clean” by deleting old deployments that are no\nlonger needed.</p>\n<h3>How do I delete an old deployment?</h3>\n<p>To do this, you will need to use an FTP client like\n<a href=\"https://cyberduck.io/?l=en\">Cyberduck</a> (or command line) to connect (or “SSH”)\ninto POSM.</p>\n<p>Power up the POSM and connect to it from your computer. Launch Cyberduck and\nstart a new connection with the following parameters:</p>\n<ul>\n<li>Type: <code>SSH</code></li>\n<li>Server: <code>posm.io</code></li>\n<li>Username: <code>root</code></li>\n<li>Password: <code>posm</code></li>\n</ul>\n<p><img src=\"/guide/troubleshooting/cyberduck.png\"></p>\n<p>You will see files under <code>/opt/data</code>. Always be extremely careful if deleting\nsubmitting field data.</p>\n<h3>How do I access the file structure behind POSM?</h3>\n<p>To do this, you will need to use an FTP client like\n<a href=\"https://cyberduck.io/?l=en\">Cyberduck</a> (or command line) to connect (or “SSH”)\ninto POSM.</p>\n<p>Power up the POSM and connect to it from your computer. Launch Cyberduck and\nstart a new connection with the following parameters:</p>\n<ul>\n<li>Type: <code>SSH</code></li>\n<li>Server: <code>posm.io</code></li>\n<li>Username: <code>root</code></li>\n<li>Password: <code>posm</code></li>\n</ul>\n<p><img src=\"/guide/troubleshooting/cyberduck.png\"></p>\n<p>The file structure looks like this: (needs completion + screenshot)</p>\n<h3>I have more than one POSM and I can’t connect to the second</h3>\n<p>Read on for the answer to the next question.</p>\n<h3>I reinstalled the POSM software and now I can’t connect</h3>\n<p>If you’ve got more than one POSM, you will not be able to connect your computer using SSH.\nYou will get errors accessing <code>posm.io</code>, Cyberduck and Terminal won’t not connect, etc.</p>\n<p>To fix this, open your <code>known_hosts</code> file (<code>~/.ssh/known_hosts</code>) in a text\neditor and delete each row that begins with <code>posm.local</code>, <code>posm.io</code>, or\n<code>172.16.1.1</code>. Save the changes and try again.</p>\n<h3>OMK was working… but stopped. I’m getting error messages from phones and computers.</h3>\n<p>This has happened when using a public fileshare with a lot of devices. In\nTerminal, connect to POSM over SSH (<code>ssh root@posm.io</code>, password <code>posm</code>) and\nthen run this command to restart OMK Server: <code>service omk-server restart</code>.</p>\n<h3>Can I push OMK/ODK data to the server through a cell connection or actual internet?</h3>\n<p>Yes… but it’s a little tricky. This would involve creating a parallel server\nin the cloud and then integrating this data back into the POSM server. If doing\nthis is worth it (or just interesting) to you, then <a href=\"https://hackmd.io/EYFhA4DYDMFMEYC0BDEBmYiSXmx4BOAY0iwHZoAGAVnnFkrICYyg\">here are the\ninstructions</a>.</p>\n<h2>Questions / statements without responses</h2>\n<!--\n* Deleting a deployment (and when)\n* MB tiles take forever\n* Directory structure\n* Why can't I connect to the wireless (bug)\n* Can I connect to the internet through POSM? (bridge mode, Android tethering)\n* Why can't I see anything in FieldPapers? (wrong centerpoint; clear cookies).\n* Why does everything look different\n* How do I add downloads\n* How do I change the password on POSM? (wifi, SSH)\n-->",
